// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.428571429;
}

.panel-heading a{
  text-decoration: none;
}

.page-header {
  color: #6F6F6F;
}

.panel-footer a{
  text-decoration: none;
}

// Admin Circle Buttons
.btn-circle {
  width: 49px;
  height: 49px;
  text-align: center;
  padding: 5px 0;
  font-size: 20px;
  line-height: 2.00;
  border-radius: 30px;
}

.btn-circle-micro {
  width: 19px;
  height: 19px;
  text-align: center;
  padding: 1px 0;
  font-size: 13px;
  line-height: 0.1;
  border-radius: 30px;
}

.btn-circle-sm {
  width: 35px;
  height: 35px;
  text-align: center;
  padding: 2px 0;
  font-size: 20px;
  line-height: 1.65;
  border-radius: 30px;
}

.btn-circle-lg {
  width: 79px;
  height: 79px;
  text-align: center;
  padding: 13px 0;
  font-size: 30px;
  line-height: 2.00;
  border-radius: 70px;
}

.select2-container .select2-selection--single {
  box-sizing: border-box;
  cursor: pointer;
  display: block;
  height: 34px;
  user-select: none;
  -webkit-user-select: none;
}

.nav > li > a{
  color: #000000;
}

.sidebar ul li a.active {
  background-color: #F90;
  color: white;
}

.alert-info {
  color: white;
  background-color: #FF9900;
  border-color: #FF9900;
}

.datepicker{
  min-width: 360px;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
  line-height: 32px;
}

.panel-default>.panel-heading {
  color: #333;
  background-color: white;
  border-color: #ddd;
}

.panel-default i{
  color:#6F6F6F;
}

.huge{
  color: #e3e3e3;
}

.image-preview-input {
  position: relative;
  overflow: hidden;
  margin: 0px;
  color: #333;
  background-color: #fff;
  border-color: #ccc;
}
.image-preview-input input[type=file] {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  padding: 0;
  font-size: 20px;
  cursor: pointer;
  opacity: 0;
  filter: alpha(opacity=0);
}
.image-preview-input-title {
  margin-left:2px;
}

.border-box {
  border: 1px solid #e3e3e3;
  padding: 5px;
}

.no-hover a{
  text-decoration: none;
}

.toast-info{
  background-color: #F90!important;
}

.pagination>.active>span, .pagination>.active>span:focus, .pagination>.active>span:hover {
  z-index: 3;
  color: #fff;
  cursor: default;
  background-color: #FF9900;
  border-color: #FF9900;
}

.custom-font{
  font-size: 18px;
  color: #ef5505;
}

.popover-content {
  padding: 0px;
}

#dynamic{
  width: 250px;
  height:auto!important;
}

.nav>li>a:focus, .nav>li>a:hover {
  text-decoration: none;
  background-color: #ECE6DD;
}

.custom-file{
  height: 33px;
}

.intro-text {
  background-color: rgba(255, 255, 255, 0.84);
  padding: 20px;
  text-align: center;
  width: 40%;
}

.text-container {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 120px;
}

.intro-text::before {
  content: "";
  position: absolute;
  top: 65%;
  margin-top: -50px;
  left: 30%;
  width: 10px;
  height: 50px;
  background: #EF5505 none repeat scroll 0% 0%;
}

.disabled{
  pointer-events: none;
}


.box {
  background:#fff;
  transition:all 0.2s ease;
  border:2px dashed #dadada;
  margin-top: 10px;
  box-sizing: border-box;
  border-radius: 5px;
  background-clip: padding-box;
  padding:0 20px 20px 20px;
  min-height:200px;
}

.box .box-content {
  padding: 16px;
  border-radius: 0 0 2px 2px;
  background-clip: padding-box;
  box-sizing: border-box;
}

.page-header{
  text-transform: capitalize;
}

.custom-panel-footer{
  background-color: #6F6F6F;
  color: white!important;
}

.panel-default{
  border: none;
}

.panel-default > .panel-heading{
  border-top: 2px dashed #dadada;
  border-left: 2px dashed #dadada;
  border-right: 2px dashed #dadada;
}

.no-hover{
  text-decoration: none;
  background-color: transparent!important;
}


